import { Bookmark, BookmarkBorderOutlined } from "@mui/icons-material";
import { Stack, FormControl,FormControlLabel, Checkbox, Box, FormLabel, FormGroup, FormHelperText, Typography } from "@mui/material";
import { useState } from "react";


export const MultiCheckBox = ()=>{
    const [accepted,setAccepted] = useState('');
    const [skills,setSkill] = useState([]);
    console.log(accepted);
    console.log(skills);
    const handleaccepted = ()=>{
        setAccepted(!accepted);
    }

    const handleSkills = (e)=>{
        const index = skills.indexOf(e.target.value);
        if (index===-1){
            setSkill([...skills,e.target.value])  // ...skills會將array所有值以文字顯示   [...陣列,值] 指的是把陣列的值展開放進去,並且固定加入某個值
  
        }else{
            setSkill(skills.filter((skill)=> skill !== e.target.value));  // filter是條件成立時，才顯示資料
        }

    }

    return (
      <Stack spacing={2}>
        <Box>
            <FormControlLabel
              label="I accept terms and conditions."
              control={
                <Checkbox
                  checked={accepted}
                  onChange={handleaccepted}
                />
              }
            />
        </Box>
        <Box>
            <Checkbox
                    checked={accepted}
                    onChange={handleaccepted}
                    icon={<BookmarkBorderOutlined/>}
                    checkedIcon={<Bookmark/>}
            />I accept terms and conditions.
            <Typography variant="body1" color="primary">{String(accepted)}</Typography>
        </Box>


        <Box>
            <FormControl error={skills.length===0? 'error':''}>
              <FormLabel>Skills</FormLabel>
              <FormGroup row>
                <FormControlLabel label="html" control={<Checkbox value="html" checked={skills.includes("html")} onChange={handleSkills}/>}></FormControlLabel>
                <FormControlLabel label="css" control={<Checkbox value="css" checked={skills.includes("css")} onChange={handleSkills}/>}></FormControlLabel>
                <FormControlLabel label="javascript" control={<Checkbox  value="javascript" checked={skills.includes("javascript")} onChange={handleSkills}/>}></FormControlLabel>
              </FormGroup>
              <FormHelperText>{skills.length===0? 'please check skills':''}</FormHelperText>
            </FormControl>
            <Typography variant="body1" color="primary">{skills.join(",")}</Typography>
        </Box>
      </Stack>
    );
}