import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import { MultiButton } from './components/MultiButton';
import { MultiTextField } from './components/MultiTextField';
import { MultiTypography } from './components/MultiTypography';
import { Layout } from './Layout';
import { MultiSelect } from './components/MultiSelect';
import { MultiRadioButton } from './components/MultiRadioButton';
import { MultiCheckBox } from './components/MultiCheckBox';
import { MultiSwitch } from './components/MultiSwitch';
import { MultiRating } from './components/MultiRating';
import { MultiAutocomplete } from './components/MultiAutocomplete';
import { MultiBox } from './components/MultiBox';


function App() {
  return (
    <div className="App">

      <BrowserRouter>
        <Routes>
          <Route path="/MUI" element={<Layout />}>
            <Route index element={<MultiTypography />}></Route>
            <Route path="button" element={<MultiButton />}></Route>
            <Route path="textfield" element={<MultiTextField />}></Route>
            <Route path="select" element={<MultiSelect />}></Route>
            <Route path="radio" element={<MultiRadioButton />}></Route>
            <Route path="checkbox" element={<MultiCheckBox />}></Route>
            <Route path="switch" element={<MultiSwitch />}></Route>
            <Route path="rating" element={<MultiRating/>}></Route>
            <Route path="autocomplete" element={<MultiAutocomplete/>}></Route>
            <Route path="box" element={<MultiBox />}></Route>
          </Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
