import { FormControl, FormControlLabel, FormGroup, FormLabel, Switch, Typography } from "@mui/material";
import { useState } from "react";

export const MultiSwitch = ()=>{
    const [state,setState] = useState(false);
    const [states,setStates] = useState([false,false,false]);
    console.log(state);
    console.log(states);
    const handelStates = (e,index)=>{
        const new_states = states.map((state,i)=>{
            if (i===index){
                return !state;
            }
            return state;
        });
        setStates(new_states);
    };
    return(
        <>

            <FormControlLabel label="Dark mode" control={<Switch/>} checked={state} onChange={(e)=>{setState(e.target.checked)}}/>
            <Typography variant="body1" color="primary">{String(state)}</Typography>
            
            <FormControl>
                <FormLabel>Items</FormLabel>
                <FormGroup row>
                    {states.map((state,index)=>{
                        return (
                            <FormControlLabel label={"item" + (index+1)} control={<Switch checked={state[index]} onChange={(e)=>handelStates(e,index)} />}></FormControlLabel>
                        );
                    })}
                </FormGroup>
            </FormControl>
            <Typography variant="body1" color="primary">{String(states)}</Typography>
        </>
    );
}