import FormControl from '@mui/material/FormControl'
import FormLabel from '@mui/material/FormLabel'
import RadioGroup from '@mui/material/RadioGroup'
import { FormControlLabel, FormHelperText, Radio, Typography } from '@mui/material'
import { useState } from 'react'

export const MultiRadioButton = () =>{

    const [jobs,setJobs] = useState('');
    console.log(jobs);
    const handleJobs = (e)=>{
        setJobs(e.target.value);
    }


    return (
      <>
        <FormControl error={!jobs? 'error':''}>
          <FormLabel id="job-experience-group-label">Years of experience</FormLabel>
          <RadioGroup aria-labelledby='job-experience-group-label' name="job-experience-group" value={jobs} onChange={handleJobs} row>
            <FormControlLabel control={<Radio size="small" color="success"/>} label="0-2" value="0-2" />
            <FormControlLabel control={<Radio/>} label="3-5" value="3-5" />
            <FormControlLabel control={<Radio/>} label="6-10" value="6-10" />
          </RadioGroup>
          <FormHelperText>{!jobs? 'choose your years of experience':''}</FormHelperText>
        </FormControl>

        <Typography variant="body1" color="primary">{jobs}</Typography>
      </>   
    );
}




/* 
    <FormLabel id="job-experience-group-label"></FormLabel>必須和<RadioGroup aria-labelledby='job-experience-group-label'> 一致 


*/