import { Favorite, FavoriteBorder } from "@mui/icons-material";
import { Box, Rating, Typography } from "@mui/material";
import { useState } from "react";

export const MultiRating = ()=>{
    const [value,setValue] = useState(null);
    console.log(value);
    return (
        <Box>
            <Typography variant="subtitle1" gutterBottom>Normal</Typography>
            <Rating value={value} onChange={(e,newValue)=>setValue(newValue)} precision={0.5} />

            <Typography variant="subtitle1" gutterBottom>Icon</Typography>
            <Rating 
                value={value} 
                onChange={(e,newValue)=>setValue(newValue)} 
                icon={<Favorite color="error"/>}
                emptyIcon={<FavoriteBorder />}
                size="large"
            />

            <Typography variant="subtitle1" gutterBottom>highlightSelectedOnly</Typography>
            <Rating 
                value={value} 
                onChange={(e,newValue)=>setValue(newValue)} 
                icon={<Favorite/>}
                emptyIcon={<FavoriteBorder/>}
                size="large"
                highlightSelectedOnly
            />

            <Typography variant="subtitle1" gutterBottom>ReadOnly</Typography>
            <Rating 
                value={value} 
                onChange={(e,newValue)=>setValue(newValue)} 
                icon={<Favorite/>}
                emptyIcon={<FavoriteBorder/>}
                size="large"
                readOnly
            />
        </Box>
    );
};

