import { Box } from "@mui/material";

export const MultiBox = ()=>{
    return (
        <>
            {/* 利用component可以轉換成任何html tag，例如：div span article section */}
            <Box component="span">test1</Box>

            {/* props的sx，類似於style可以設定css */}
            <Box component="span" sx={{
                backgroundColor:"primary.main",
                color:"white",
                width:"100px",
                height:"100px",
                padding:"16px",
                border: 5,
                borderColor: 'secondary.main',
                ':hover':{
                    backgroundColor:"primary.light"
                }
            }}>test2</Box>


            {/* Box也可以直接設定相關屬性，而且屬性名稱比html屬性或css簡短 */}
            <Box display="flex" width="100px" height="100px" p={2} bgcolor="success.main" border={2} >test3</Box>

            <Box>
                <Box sx={{
                    width:"100px",
                    height:"100px",
                    backgroundColor:"error.main",
                    display:{
                        xs:"block",
                        md:"inline-block"
                    }
                }}>test4</Box>
                <Box sx={{
                    width:"100px",
                    height:"100px",
                    backgroundColor:"info.main",
                    display:{
                        xs:"block",
                        md:"inline-block"
                    }
                }}>test5</Box>
            </Box>
        </>
    );
}

// 為何要用Box取代div或其他框架的html tag，原因是box的sx屬性可以使用mui內建的樣式
// 可在官網 Style utilities查詢所有css對應的的props
// 要使用display的功用，必須放在container中，例如：<div>、<Box>中