import { Autocomplete, Stack, TextField } from "@mui/material";
import { useState } from "react";

export const MultiAutocomplete = ()=>{

    const data= ["javascript","python","html","css","java","vbscript"];
    const [skill,setSkill] = useState("");
    const [skills,setSkills] = useState([]);
    console.log(skill);
    console.log(skills);
    return (
        <Stack spacing={2} width="250px">
            <Autocomplete 
                options={data}
                renderInput={(param)=> <TextField {...param} label="Skills" />}
                value={skill}
                onChange={(e,newValue)=>setSkill(newValue)}   // 不能使用e.target.value，因為Autocomplete必須按下enter後，value才會有值
                freeSolo         // 使用者輸入任何值都能儲存
                
            />


            <Autocomplete 
                options={data}
                renderInput={(param)=> <TextField {...param} label="Skills" />}
                value={skills}
                onChange={(e,newValue)=>setSkills(newValue)}
                freeSolo         // 使用者輸入任何值都能儲存 
                multiple         // 多選
                filterSelectedOptions  //去除已選過的
            />
            
        </Stack>

    );
}


// e.target.value是取得「props的value」值，而Autocomplete雖有設定value屬性